.hero {
  max-width: 100%;
  padding: 0.725rem;
}

.hero-text {
  margin-top: 3rem;
}

.hero-subheading {
  font-family: "Rajdhani", sans-serif;
  font-weight: light;
  font-size: 1.75rem;
  margin: 0;
  color: #f2b872;
}

.hero-heading {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 3.5rem;
  line-height: 2.75rem;
  letter-spacing: 18;
}

.text-decoration {
  width: 6.25rem;
  height: 0.313rem;
  background-color: #f2b872;
  margin-top: 1rem;
}

.hero-paragraph {
  font-size: 0.875rem;
  opacity: 60%;
  padding-right: 0.313rem;
  margin-top: 2rem;
}

.highlight {
  color: #f2b872;
  font-weight: bold;
  font-size: 1rem;
  opacity: 100%;
}

.join-today {
  width: 150px;
  height: 40px;
  background-color: #f2b872;
  border: none;
  margin-top: 50px;
  font-size: 1.125rem;
  font-weight: bold;
  border-radius: 5px;
}

.join-today:hover {
  background-color: #000;
  color: #f2b872;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.join-today:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: -20px;
  width: 20px;
  height: 100%;
  background-color: #fff;
  opacity: 40%;
  animation: shine 0.7s ease;
}

@keyframes shine {
  0% {
    left: -20px;
  }
  100% {
    left: 170px;
  }
}

.hero-image {
  width: 100%;
  margin-bottom: -1.125rem;
  margin-top: -2rem;
}
.hero-image img {
  width: 100%;
  object-fit: contain;
}
