.trainers {
  display: flex;
  max-width: 100%;
  padding: 0.725rem;
  z-index: 1;
  position: relative;
  justify-content: center;
  flex-direction: column;
}

.trainers .shadow {
  color: #262626;
}

.trainers-heading {
  font-size: 2.265rem;
  margin: 0;
  color: #000;
  z-index: 10;
  position: absolute;
  top: 0;
  margin-top: 2.625rem;
}

.trainers .text-decoration {
  margin-top: 5.25rem;
}

.trainers-cards {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.trainers-card {
  width: 17.188rem;
  height: 18.75rem;
  border-radius: 25px;
  border: 5px solid #f2b872;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  margin-top: 6.25rem;
  cursor: pointer;
}

.trainers-card-text {
  position: absolute;
  text-align: center;
  top: 0;
  transition: top 0.5s ease-in-out;
}

.trainers-card-text img {
  width: 9.375rem;
  margin-top: 1.25rem;
}

.trainers-card-description {
  display: none;
  flex-direction: column;
  padding: 0 20px;
  margin-top: 200px;
}

.trainer-card-experience {
  width: 100%;
  text-align: center;
}

.trainers-card-description-text {
  text-align: center;
  font-size: 14px;
  font: bold;
  width: 100%;
  margin-bottom: 100px;
}

.trainers-card:hover .trainers-card-text {
  top: -100px;
}

.trainers-card:hover {
  background-color: #f2b872;
}

.trainers-card:hover .trainers-card-description {
  display: block;
  color: #000;
}

.trainers-card.show-description .trainers-card-description {
  display: flex;
  color: #000;
}
