.pricing {
  display: flex;
  max-width: 100%;
  padding: 0.725rem;
  z-index: 1;
  position: relative;
  justify-content: center;
  flex-direction: column;
}

.pricing .shadow {
  color: #262626;
}

.pricing-heading {
  font-size: 2.265rem;
  margin: 0;
  color: #000;
  z-index: 10;
  position: absolute;
  top: 0;
  margin-top: 2.625rem;
}

.pricing .text-decoration {
  margin-top: 5.25rem;
}

.pricing-paragraph {
  margin-top: 20px;
  margin-bottom: 50px;
}

.pricing-table {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.pricing-card {
  position: relative;
  width: 320px;
  height: 612px;
  overflow: hidden;
  border-radius: 20px;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 5px 5px 9px 5px rgba(0, 0, 0, 0.25);
}

.card-decoration {
  position: absolute;
  top: 0;
  left: 0;
}

.card-decoration-text {
  color: white;
  position: absolute;
  left: 20px;
  top: 70px;
}

.pricing-price {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-direction: column;
  margin-right: 50px;
}

.pricing-price-tag {
  font-weight: normal;
  font-family: "Roboto", sans-serif;
  font-size: 48px;
  line-height: 71px;
  margin-top: 60px;
}

.pricing-price-description {
  margin-top: -20px;
  font-size: 18px;
  font-weight: lighter;
  margin-left: 10px;
}

.card-benefits {
  width: 100%;
  display: flex;
  padding: 0 20px;
  justify-content: space-between;
  flex-direction: column;
  margin-top: 50px;
}

.card-benefits-option {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  margin-left: 40px;
}

.card-benefits-option img {
  max-width: 20px;
}

.card-benefits-option p {
  font-size: 14px;
  font-weight: normal;
}
